<template>
<div style=" margin: 0 auto; padding-bottom: 30px;">
    <div class="className" id="multipleBarsLines4" style="height:260px;padding: 0 30px;width:95%; "></div>
</div>
</template>

<script>
import echarts from "echarts";
export default {
    props: {
        seriesBars:{
            type:Object,
            default(){
                return {}
            }
        },
    },
    watch: {
        seriesBars (newVal, oldVal) {
            this.title = newVal.title
            this.agent = newVal.agent.value;
            this.target = newVal.target.value;
            this.analysis = newVal.analysis.value
            this.courseNames = newVal.valueTable;
            this.initChart2();
        }
    },
    data() {
        return {
            chart: null,
            agent: [],
            target: [],
            courseNames: [],
            analysis:[],
            title:''
        };
    },
    created() {
        setTimeout(() =>{
            if(this.seriesBars.length > 0){
                this.title = this.seriesBars.title
                this.agent = this.seriesBars.agent.value;
                this.target = this.seriesBars.target.value;
                this.analysis = this.seriesBars.analysis.value
                this.courseNames = this.seriesBars.valueTable;
                this.initChart2();
            }
        },500);
    },
    methods:{
        initChart2() {
            this.chart = echarts.init(document.getElementById("multipleBarsLines4"));
            var option = {
                tooltip: {
                    position: function (point) {
                        return [point[0], "15%"];
                    },
                    // formatter: function(p) {
                    //   var index = p.dataIndex;
                    //   return `${p.name} <br/> ${p.seriesName} : ${p.value}<br/>`;
                    // }
                },
                //   小图标   折线  转换柱状图
                toolbox: {
                    show: true,
                    top: -5,
                    feature: {
                        saveAsImage: {
                            show: true,
                        },
                    }
                },
                title: {
                    text: `${this.title}`,
                    textStyle: {
                      color: '#aaa'
                    }
                },
                legend: {
                    data: ["直接达成度", "间接达成度","期望达成度"],
                    right: 21,
                    top: 0,
                    textStyle: {
                        fontSize: 16,
                        color: "#ccc"
                    }
                },
                grid: {
                    left: "3%",
                    right: "4%",
                    bottom: "10%",
                    containLabel: true
                },
                xAxis: {
                    axisLabel: {
                        fontSize: 14,
                        color: "rgba(3,177,135,1)"
                    },
                    axisLine: {
                        show: false,
                        lineStyle: {
                            color: "#aaa",
                            show: false
                        }
                    },
                    axisTick: {
                        show: false,
                        color: '#323232'
                    },
                    data: this.courseNames,
                    axisLabel: {
                        //坐标轴刻度标签的相关设置。this.courseNames,
                        interval: 0,
                        rotate: this.courseNames.length > 10 ? 45 : 0 //代表逆时针旋转45度
                    }
                },
                dataZoom: [{
                    type: "slider",
                    show: true,
                    xAxisIndex: [0],
                    left: "3%",
                    bottom: -25,
                    start: 0,
                    end: 90 //初始化滚动条
                }],
                yAxis: {
                    type: 'value',
                    axisLine: {
                        show: false,
                        lineStyle: {
                            color: "#969696"
                        }
                    },
                    splitLine: {
                        show: true,
                        lineStyle: {
                            color: '#F4F4F4'
                        }
                    },
                    axisTick: {
                        show: false,
                        color: '#323232'
                    },
                },
                series: [{
                        name: "直接达成度",
                        type: "bar",
                        barGap: "0%",
                        barCategoryGap: "80%",
                        itemStyle: {
                            barBorderRadius: [5,5,0,0],
                            color: {
                                type: 'linear',
                                x: 0,
                                y: 0,
                                x2: 0,
                                y2: 1,
                                colorStops: [{
                                    offset: 1, color: '#8EE9CA' // 0% 处的颜色
                                }, {
                                    offset: 0, color: '#03B187' // 100% 处的颜色
                                }],
                            }
                        },
                        barWidth:30,
                        label: {
                            show: false
                        },
                        data: this.analysis
                    },
                    {
                        name: "间接达成度",
                        type: "bar",
                        barCategoryGap: "50%",
                        itemStyle: {
                            barBorderRadius: [5,5,0,0],
                            color: {
                                type: 'linear',
                                x: 0,
                                y: 0,
                                x2: 0,
                                y2: 1,
                                colorStops: [{
                                    offset: 0, color: '#FFA351' // 0% 处的颜色
                                }, {
                                    offset: 1, color: '#FFE098' // 100% 处的颜色
                                }],
                            }
                        },
                        barWidth:30,
                        // barWidth:40,
                        label: {
                            show: false
                        },
                        data: this.agent
                    },
                    {
                        name: "期望达成度",
                        type: "bar",
                        barCategoryGap: "50%",
                        itemStyle: {
                            barBorderRadius: [5,5,0,0],
                            color: {
                                type: 'linear',
                                x: 0,
                                y: 0,
                                x2: 0,
                                y2: 1,
                                colorStops: [{
                                    offset: 0, color: '#B3DCFF' // 0% 处的颜色
                                }, {
                                    offset: 1, color: '#5AB3FF' // 100% 处的颜色
                                }],
                            }
                        },
                        barWidth:30,
                        // barWidth:40,
                        label: {
                            show: false
                        },
                        data: this.target
                    },
                ]
            };
            this.chart.setOption(option);
        }
    }
};
</script>



