<template>
<div class="CalculateDegree padding students analysis ">
    <div class="breadcrumb-pd">
        <el-breadcrumb separator=">">
            <el-breadcrumb-item :to="{ path: '/Calculate/CalculateDegree' }">达成度计算</el-breadcrumb-item>
            <el-breadcrumb-item :to="{ path: '/Calculate/CalculateDegree' }">毕业要求</el-breadcrumb-item>
            <el-breadcrumb-item :to="{ path: '/Calculate/GraduationAch' }">毕业要求指标点</el-breadcrumb-item>
            <el-breadcrumb-item :to="{ path: '/Calculate/pointAnalysis' }">支撑课程</el-breadcrumb-item>
            <el-breadcrumb-item class="color_zhi">
                课程目标
            </el-breadcrumb-item>
        </el-breadcrumb>
    </div>
    <div class="analysis1">
        <div class="analysis1_top">
            <div class="analysis1_left" @click="goback">
              <svgIcon icon-class="iconfanhui" size="14"></svgIcon>
                <span>返回上级</span>
            </div>
            <p v-if="nametitle">{{nametitle}}毕业要求达成度分析</p>
            <div class="analysis1_right">
              <svgIcon icon-class="icondaochu-excel" size="14"></svgIcon>
                <span>导出Excel</span>
            </div>
        </div>
        <div class="analysis1_botm">
            <img src="@/assets/img/dachengdu05.png" alt="" :style="{'width': fullWidth == 1920 ? '1131px' : '83%'}">
        </div>
    </div>
    <div class="echarts4" >
        <!-- 柱状图引用 -->
        <echarts4 :seriesBars="seriesBarData"></echarts4>
    </div>
    <div class="analysis_body">
        <!-- <div class="analysis_body_echarts">
            <series-bar id="bar-02" :series-bar-data="seriesBarData" :color="colorArr" legendY="top" chartHeight="400px" />
        </div> -->
       
        <div class="analysis_body_table">
            <el-table :data="tableData" stripe style="width: 100%">
                <el-table-column prop="goalname" label="课程目标" align="center">
                </el-table-column>
                <el-table-column prop="point" label="对应能力培养" align="center">
                </el-table-column>
                <el-table-column prop="examines" label="考核方式" align="center">
                </el-table-column>
                <el-table-column prop="score" label="考核分值" align="center">
                </el-table-column>
                <el-table-column prop="zhanbi" label="考核分值占比" align="center">
                </el-table-column>
                <el-table-column prop="analysis" label="直接达成度" align="center">
                </el-table-column>
                <el-table-column prop="agent" label="间接达成度" align="center" >
                    <template slot-scope="scope">
                        <span >{{ scope.row.agent == 'null'? '': scope.row.agent}}</span>
                    </template>
                </el-table-column>
                <el-table-column prop="target" label="期望达成度" align="center">
                </el-table-column>
                <!-- <el-table-column prop="analysis" label="达成值" align="center">
                </el-table-column>
                <el-table-column prop="point" label="支撑指标点">
                </el-table-column> -->

                <!-- <el-table-column v-for="(item, index) in header" :key="index" :prop="item" :label="item" align="center">
                </el-table-column> -->
            </el-table>
        </div>
    </div>
</div>
</template>

<script>
// import SeriesBar from '../components/Echarts/SeriesBar'
import echarts4 from "@/components/echaars/echars4"
export default {
    name: 'CourseAnalysis',
    components: {
        echarts4
    },
    data() {
        return {
            nametitle:this.$route.query.nametitle || '',
            pointList: [],
            title: '',
            gradeOptions: [],
            classOptions: [],
            studentOptions: [],
            seriesBarData: {},
            colorArr: ['#d87c7c', '#919e8b'],
            tableData: [],
            header: [],
            form: {
                courseId: '',
                grade: '',
                classId: '',
                studentId: ''
            },
            api: {
                grades: '/basic/achieve/grades',
                allClass(scope) {
                    return `/basic/achieve/classes/${scope.grade}`
                },
                allStudent(scope) {
                    return `/basic/achieve/students/${scope.classId}`
                },
                getGoals: '/basic/achieve/analysis/goals'
            },
            fullWidth:document.documentElement.clientWidth //计算屏幕的宽度
        }
    },
    created() {
        this.form = this.$route.query
        delete this.form.pointId
    },
    mounted() {
        this.grades()
        if (this.form.grade) {
            this.allClass()
        }
        if (this.form.classId) {
            this.allStudent()
        }
        this.getGoals()
    },
    methods: {
        goback(){
            this.$router.go(-1)
        },
        gradeChange() {
            this.allClass()
        },
        classChange() {
            this.allStudent()
        },
        //年级查询
        grades() {
            this.$get(this.api.grades).then(res => {
                if (res.status === 1) {
                    this.gradeOptions = res.data
                }
            })
        },
        //班级查询
        allClass() {
            const url = this.api.allClass({
                grade: this.form.grade
            })
            this.$get(url).then(res => {
                if (res.status === 1) {
                    this.classOptions = res.data
                }
            })
        },
        //学生查询
        allStudent() {
            const url = this.api.allStudent({
                classId: this.form.classId
            })
            this.$get(url).then(res => {
                if (res.status === 1) {
                    this.studentOptions = res.data
                }
            })
        },
        getGoals() {
            this.$post(this.api.getGoals, this.form).then(res => {
                if (res.status === 1) {
                    this.title = res.data.title
                    //表格
                    this.header = res.data.header.slice(3)
                    this.tableData = res.data.goals
                    this.seriesBarData = res.data
                } else {
                    this.$notify.error({
                        title: 'error',
                        duration: 1500,
                        message: res.message
                    })
                }
            })
        },
        calculate() {
            this.getGoals()
        }
    }
}
</script>

<style lang="scss" >
.analysis {
    min-width: 1021px;
     overflow: hidden;
    margin-bottom: 80px;
    .breadcrumb-pd{
        padding: 33px 0px ;
        .el-breadcrumb__item:last-child .el-breadcrumb__inner{
            color: #03B187;
        }
        .el-breadcrumb__inner.is-link, .el-breadcrumb__inner a{
            color: #696969;
        }
    }
    .analysis1{
        box-sizing: border-box;
        width: 100%;
        height: 196px;
        background: url("../../assets/img/bg-chaxun.png") no-repeat;
        background-size: 100%;
        margin-bottom: 20px;
        .analysis1_top{
            padding: 28px 0 1vw 1vw ;
            padding-right: 1vw;
            display: flex;
            .analysis1_left{
                flex:0.1;
                width:120px;
                height:28px;
                border:1px solid rgba(176,249,224,1);
                border-radius:14px;
                cursor: pointer;
                text-align: center;
                img{
                    width: 12%;
                }
                span{
                    font-size:14px;
                    font-family:PingFang SC;
                    font-weight:500;
                    color:rgba(176,249,224,1);
                    line-height:30px;
                }
            }
            p{  
                flex:1;
                text-align: center;
                font-size:24px;
                font-family:PingFang SC;
                font-weight:bold;
                color:rgba(255,255,255,1);
                // margin-left: 10vw;
            }
            .analysis1_right{
                flex:0.1;
                width:120px;
                height:28px;
                border:1px solid rgba(176,249,224,1);
                border-radius:14px;
                padding-left: 5px;
                padding-right: 5px;
                text-align: center;
                img{
                    width: 12%;
                }
                span{
                    font-size:14px;
                    font-family:PingFang SC;
                    font-weight:500;
                    color:rgba(176,249,224,1);
                    line-height:30px;
                }
            }
        }
        .analysis1_botm{
            margin-top: 22px;
            // padding-left:5vw;
            // padding-right: 5vw;
            img{
                margin: 0 auto;
                display: flex;
                // width: 100%;
            }
        }
    }
    .analysis_header {
        overflow: hidden;
        position: relative;
        margin: 40px 0 45px 0;

        .margin-r {
            margin-right: 20px;
        }

        .float-l {
            float: left;
        }

        .calculate-btn {
            position: absolute;
            right: 0;
            top: -20%;
        }
    }

    .echarts4 {
       background-color: #fff;
        padding-top: 25px;
    }

    .analysis_body {
        background-color: #fff;
        margin-top: 20px;
        .analysis_body_title {
            margin-bottom: 40px;
            padding: 25px 35px;

            span:nth-child(1) {
                float: left;
                font-weight: 700;
                font-size: 16px;
            }

            span:nth-child(2) {
                float: right;
                border: 1px solid #DCDFE6;
                font-size: 14px;
                padding: 5px 8px;
            }
        }

        .analysis_body_table {
            width: 100%;
            margin: 30px auto;
            padding-bottom: 40px;
        }
    }
}
</style>



